import { CapacitorSQLite } from '@capacitor-community/sqlite';
import { Capacitor } from '@capacitor/core';

export const initializeDatabase = async () => {
  try {
    // Only initialize SQLite on mobile platforms
    if (Capacitor.getPlatform() !== 'web') {
      const db = await CapacitorSQLite.open({ database: 'fitpro.db' });
      console.log('Database opened successfully');
      await createTables(db);
    } else {
      console.warn('SQLite is not supported on the web platform.');
      // You can add a fallback mechanism here for the web platform if needed.
    }
  } catch (error) {
    console.error('Error initializing database', error);
  }
};

const createTables = async (db) => {
  try {
    // Create exercises table
    await db.executeSql(
      `CREATE TABLE IF NOT EXISTS exercises (
        id INTEGER PRIMARY KEY AUTOINCREMENT,
        name TEXT NOT NULL,
        region TEXT,
        primary_muscle TEXT,
        secondary_muscle TEXT,
        notes TEXT
      );`
    );

    // Create workouts table
    await db.executeSql(
      `CREATE TABLE IF NOT EXISTS workouts (
        id INTEGER PRIMARY KEY AUTOINCREMENT,
        name TEXT NOT NULL,
        archived INTEGER DEFAULT 0
      );`
    );

    // Create workout_exercises table
    await db.executeSql(
      `CREATE TABLE IF NOT EXISTS workout_exercises (
        id INTEGER PRIMARY KEY AUTOINCREMENT,
        workout_id INTEGER NOT NULL,
        exercise_id INTEGER NOT NULL,
        sets INTEGER NOT NULL,
        reps INTEGER NOT NULL,
        weight REAL,
        FOREIGN KEY(workout_id) REFERENCES workouts(id),
        FOREIGN KEY(exercise_id) REFERENCES exercises(id)
      );`
    );

    // Create logs table
    await db.executeSql(
      `CREATE TABLE IF NOT EXISTS logs (
        id INTEGER PRIMARY KEY AUTOINCREMENT,
        workout_id INTEGER,
        exercise_id INTEGER,
        date TEXT NOT NULL,
        sets INTEGER NOT NULL,
        reps INTEGER NOT NULL,
        weight REAL,
        notes TEXT,
        FOREIGN KEY(workout_id) REFERENCES workouts(id),
        FOREIGN KEY(exercise_id) REFERENCES exercises(id)
      );`
    );

    console.log('Tables created or already exist.');
  } catch (error) {
    console.error('Error creating tables', error);
  }
};
