import React, { useEffect } from 'react';
import { IonApp, IonTabs, IonRouterOutlet, IonTabBar, IonTabButton, IonIcon, IonLabel } from '@ionic/react';
import { home, fitness, barChart, list, analyticsOutline } from 'ionicons/icons';
import { initializeDatabase } from './database/database';
import { BrowserRouter as Router, Route, Redirect } from 'react-router-dom';

// Importing pages
import Dashboard from './pages/Dashboard';
import Workouts from './pages/Workouts';
import Exercises from './pages/Exercises';
import Logs from './pages/Logs';
import Analytics from './pages/Analytics';

import '@ionic/react/css/core.css';

const App = () => {
  useEffect(() => {
    // Initialize the database on app load
    initializeDatabase();
    console.log('Database initialized');
  }, []);

  return (
    <IonApp>
      <Router>
        <IonTabs>
          <IonRouterOutlet>
            {/* Define routes */}
            <Route path="/dashboard" component={Dashboard} exact />
            <Route path="/workouts" component={Workouts} />
            <Route path="/exercises" component={Exercises} />
            <Route path="/logs" component={Logs} />
            <Route path="/analytics" component={Analytics} />
            {/* Default redirect to Dashboard */}
            <Redirect exact from="/" to="/dashboard" />
          </IonRouterOutlet>

          {/* Bottom Tab Navigation */}
          <IonTabBar slot="bottom">
            <IonTabButton tab="dashboard" href="/dashboard">
              <IonIcon icon={home} />
              <IonLabel>Dashboard</IonLabel>
            </IonTabButton>
            <IonTabButton tab="workouts" href="/workouts">
              <IonIcon icon={fitness} />
              <IonLabel>Workouts</IonLabel>
            </IonTabButton>
            <IonTabButton tab="exercises" href="/exercises">
              <IonIcon icon={list} />
              <IonLabel>Exercises</IonLabel>
            </IonTabButton>
            <IonTabButton tab="logs" href="/logs">
              <IonIcon icon={barChart} />
              <IonLabel>Logs</IonLabel>
            </IonTabButton>
            <IonTabButton tab="analytics" href="/analytics">
              <IonIcon icon={analyticsOutline} />
              <IonLabel>Analytics</IonLabel>
            </IonTabButton>
          </IonTabBar>
        </IonTabs>
      </Router>
    </IonApp>
  );
};

export default App;
